var absoluteValue = require('./absoluteValue');

var Node = require('../../node');

var NthRoot = require('./nthRoot');

var TreeSearch = require('../../TreeSearch');

var FUNCTIONS = [NthRoot.nthRoot, absoluteValue]; // Searches through the tree, prioritizing deeper nodes, and evaluates
// functions (e.g. abs(-4)) if possible.
// Returns a Node.Status object.

var search = TreeSearch.postOrder(functions); // Evaluates a function call if possible. Returns a Node.Status object.

function functions(node) {
  if (!Node.Type.isFunction(node)) {
    return Node.Status.noChange(node);
  }

  for (var i = 0; i < FUNCTIONS.length; i++) {
    var nodeStatus = FUNCTIONS[i](node);

    if (nodeStatus.hasChanged()) {
      return nodeStatus;
    }
  }

  return Node.Status.noChange(node);
}

module.exports = search;