/*
 * Performs simpifications that are more basic and overaching like (...)^0 => 1
 * These are always the first simplifications that are attempted.
 */
var Node = require('../../node');

var TreeSearch = require('../../TreeSearch');

var convertMixedNumberToImproperFraction = require('./convertMixedNumberToImproperFraction');

var rearrangeCoefficient = require('./rearrangeCoefficient');

var reduceExponentByZero = require('./reduceExponentByZero');

var reduceMultiplicationByZero = require('./reduceMultiplicationByZero');

var reduceZeroDividedByAnything = require('./reduceZeroDividedByAnything');

var removeAdditionOfZero = require('./removeAdditionOfZero');

var removeDivisionByOne = require('./removeDivisionByOne');

var removeExponentBaseOne = require('./removeExponentBaseOne');

var removeExponentByOne = require('./removeExponentByOne');

var removeMultiplicationByNegativeOne = require('./removeMultiplicationByNegativeOne');

var removeMultiplicationByOne = require('./removeMultiplicationByOne');

var simplifyDoubleUnaryMinus = require('./simplifyDoubleUnaryMinus');

var SIMPLIFICATION_FUNCTIONS = [// convert mixed numbers to improper fractions
convertMixedNumberToImproperFraction, // multiplication by 0 yields 0
reduceMultiplicationByZero, // division of 0 by something yields 0
reduceZeroDividedByAnything, // ____^0 --> 1
reduceExponentByZero, // Check for x^1 which should be reduced to x
removeExponentByOne, // Check for 1^x which should be reduced to 1
// if x can be simplified to a constant
removeExponentBaseOne, // - - becomes +
simplifyDoubleUnaryMinus, // If this is a + node and one of the operands is 0, get rid of the 0
removeAdditionOfZero, // If this is a * node and one of the operands is 1, get rid of the 1
removeMultiplicationByOne, // In some cases, remove multiplying by -1
removeMultiplicationByNegativeOne, // If this is a / node and the denominator is 1 or -1, get rid of it
removeDivisionByOne, // e.g. x*5 -> 5x
rearrangeCoefficient];
var search = TreeSearch.preOrder(basics); // Look for basic step(s) to perform on a node. Returns a Node.Status object.

function basics(node) {
  for (var i = 0; i < SIMPLIFICATION_FUNCTIONS.length; i++) {
    var nodeStatus = SIMPLIFICATION_FUNCTIONS[i](node);

    if (nodeStatus.hasChanged()) {
      return nodeStatus;
    } else {
      node = nodeStatus.newNode;
    }
  }

  return Node.Status.noChange(node);
}

module.exports = search;