var ChangeTypes = require('../../ChangeTypes');

var Node = require('../../node'); // If `node` is a multiplication node with 0 as one of its operands,
// reduce the node to 0. Returns a Node.Status object.


function reduceMultiplicationByZero(node) {
  if (node.op !== '*') {
    return Node.Status.noChange(node);
  }

  var zeroIndex = node.args.findIndex(function (arg) {
    if (Node.Type.isConstant(arg) && arg.value === '0') {
      return true;
    }

    if (Node.PolynomialTerm.isPolynomialTerm(arg)) {
      var polyTerm = new Node.PolynomialTerm(arg);
      return polyTerm.getCoeffValue() === 0;
    }

    return false;
  });

  if (zeroIndex >= 0) {
    // reduce to just the 0 node
    var newNode = Node.Creator.constant(0);
    return Node.Status.nodeChanged(ChangeTypes.MULTIPLY_BY_ZERO, node, newNode);
  } else {
    return Node.Status.noChange(node);
  }
}

module.exports = reduceMultiplicationByZero;