var Node = require('../node');

var NthRoot = require('../simplifyExpression/functionsSearch/nthRoot'); // Function to check if nthRoot nodes can be multiplied
// e.g. nthRoot(x, 2) * nthRoot(x, 2) -> true
// e.g. nthRoot(x, 2) * nthRoot(x, 3) -> false


function canMultiplyLikeTermsNthRoots(node) {
  // checks if node is a multiplication of nthRoot nodes
  // all the terms has to have the same root node to be multiplied
  if (!Node.Type.isOperator(node, '*') || !node.args.every(function (term) {
    return Node.Type.isFunction(term, 'nthRoot');
  })) {
    return false;
  } // Take arbitrary root node


  var firstTerm = node.args[0];
  var rootNode = NthRoot.getRootNode(firstTerm);
  return node.args.every(function (term) {
    return NthRoot.getRootNode(term).equals(rootNode);
  });
}

module.exports = canMultiplyLikeTermsNthRoots;