/*
  For determining the type of a mathJS node.
 */
var NodeType = {};

NodeType.isOperator = function (node) {
  var operator = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : null;
  return node.type === 'OperatorNode' && node.fn !== 'unaryMinus' && '*+-/^'.includes(node.op) && (operator ? node.op === operator : true);
};

NodeType.isParenthesis = function (node) {
  return node.type === 'ParenthesisNode';
};

NodeType.isUnaryMinus = function (node) {
  return node.type === 'OperatorNode' && node.fn === 'unaryMinus';
};

NodeType.isFunction = function (node) {
  var functionName = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : null;

  if (node.type !== 'FunctionNode') {
    return false;
  }

  if (functionName && node.fn.name !== functionName) {
    return false;
  }

  return true;
};

NodeType.isSymbol = function (node) {
  var allowUnaryMinus = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : false;

  if (node.type === 'SymbolNode') {
    return true;
  } else if (allowUnaryMinus && NodeType.isUnaryMinus(node)) {
    return NodeType.isSymbol(node.args[0], false);
  } else {
    return false;
  }
};

NodeType.isConstant = function (node) {
  var allowUnaryMinus = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : false;

  if (node.type === 'ConstantNode') {
    return true;
  } else if (allowUnaryMinus && NodeType.isUnaryMinus(node)) {
    if (NodeType.isConstant(node.args[0], false)) {
      var value = parseFloat(node.args[0].value);
      return value >= 0;
    } else {
      return false;
    }
  } else {
    return false;
  }
};

NodeType.isConstantFraction = function (node) {
  var allowUnaryMinus = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : false;

  if (NodeType.isOperator(node, '/')) {
    return node.args.every(function (n) {
      return NodeType.isConstant(n, allowUnaryMinus);
    });
  } else {
    return false;
  }
};

NodeType.isConstantOrConstantFraction = function (node) {
  var allowUnaryMinus = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : false;

  if (NodeType.isConstant(node, allowUnaryMinus) || NodeType.isConstantFraction(node, allowUnaryMinus)) {
    return true;
  } else {
    return false;
  }
};

NodeType.isIntegerFraction = function (node) {
  var allowUnaryMinus = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : false;

  if (!NodeType.isConstantFraction(node, allowUnaryMinus)) {
    return false;
  }

  var numerator = node.args[0];
  var denominator = node.args[1];

  if (allowUnaryMinus) {
    if (NodeType.isUnaryMinus(numerator)) {
      numerator = numerator.args[0];
    }

    if (NodeType.isUnaryMinus(denominator)) {
      denominator = denominator.args[0];
    }
  }

  return Number.isInteger(parseFloat(numerator.value)) && Number.isInteger(parseFloat(denominator.value));
};

module.exports = NodeType;