var checks = require('../../checks');

var clone = require('../../util/clone');

var ChangeTypes = require('../../ChangeTypes');

var Node = require('../../node'); // Rearranges something of the form x * 5 to be 5x, ie putting the coefficient
// in the right place.
// Returns a Node.Status object


function rearrangeCoefficient(node) {
  if (!checks.canRearrangeCoefficient(node)) {
    return Node.Status.noChange(node);
  }

  var newNode = clone(node);
  var polyNode = new Node.PolynomialTerm(newNode.args[0]);
  var constNode = newNode.args[1];
  var exponentNode = polyNode.getExponentNode();
  newNode = Node.Creator.polynomialTerm(polyNode.getSymbolNode(), exponentNode, constNode);
  return Node.Status.nodeChanged(ChangeTypes.REARRANGE_COEFF, node, newNode);
}

module.exports = rearrangeCoefficient;