var arithmeticSearch = require('../arithmeticSearch');

var clone = require('../../util/clone');

var divideByGCD = require('./divideByGCD');

var Node = require('../../node'); // Simplifies a polynomial term with a fraction as its coefficients.
// e.g. 2x/4 --> x/2    10x/5 --> 2x
// Also simplified negative signs
// e.g. -y/-3 --> y/3   4x/-5 --> -4x/5
// returns the new simplified node in a Node.Status object


function simplifyPolynomialFraction(node) {
  if (!Node.PolynomialTerm.isPolynomialTerm(node)) {
    return Node.Status.noChange(node);
  }

  var polyNode = new Node.PolynomialTerm(clone(node));

  if (!polyNode.hasFractionCoeff()) {
    return Node.Status.noChange(node);
  }

  var coefficientSimplifications = [divideByGCD, // for integer fractions
  arithmeticSearch // for decimal fractions
  ];

  for (var i = 0; i < coefficientSimplifications.length; i++) {
    var coefficientFraction = polyNode.getCoeffNode(); // a division node

    var newCoeffStatus = coefficientSimplifications[i](coefficientFraction);

    if (newCoeffStatus.hasChanged()) {
      // we need to reset change groups because we're creating a new node
      var newCoeff = Node.Status.resetChangeGroups(newCoeffStatus.newNode);

      if (newCoeff.value === '1') {
        newCoeff = null;
      }

      var exponentNode = polyNode.getExponentNode();
      var newNode = Node.Creator.polynomialTerm(polyNode.getSymbolNode(), exponentNode, newCoeff);
      return Node.Status.nodeChanged(newCoeffStatus.changeType, node, newNode);
    }
  }

  return Node.Status.noChange(node);
}

module.exports = simplifyPolynomialFraction;