var clone = require('../../util/clone');

var ChangeTypes = require('../../ChangeTypes');

var Node = require('../../node'); // Simplifies two unary minuses in a row by removing both of them.
// e.g. -(- 4) --> 4


function simplifyDoubleUnaryMinus(node) {
  if (!Node.Type.isUnaryMinus(node)) {
    return Node.Status.noChange(node);
  }

  var unaryArg = node.args[0]; // e.g. in - -x, -x is the unary arg, and we'd want to reduce to just x

  if (Node.Type.isUnaryMinus(unaryArg)) {
    var newNode = clone(unaryArg.args[0]);
    return Node.Status.nodeChanged(ChangeTypes.RESOLVE_DOUBLE_MINUS, node, newNode);
  } // e.g. - -4, -4 could be a constant with negative value
  else if (Node.Type.isConstant(unaryArg) && parseFloat(unaryArg.value) < 0) {
      var _newNode = Node.Creator.constant(parseFloat(unaryArg.value) * -1);

      return Node.Status.nodeChanged(ChangeTypes.RESOLVE_DOUBLE_MINUS, node, _newNode);
    } // e.g. -(-(5+2))
    else if (Node.Type.isParenthesis(unaryArg)) {
        var parenthesisNode = unaryArg;
        var parenthesisContent = parenthesisNode.content;

        if (Node.Type.isUnaryMinus(parenthesisContent)) {
          var _newNode2 = Node.Creator.parenthesis(parenthesisContent.args[0]);

          return Node.Status.nodeChanged(ChangeTypes.RESOLVE_DOUBLE_MINUS, node, _newNode2);
        }
      }

  return Node.Status.noChange(node);
}

module.exports = simplifyDoubleUnaryMinus;